:root {
  background-color: white;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

body {
  background-color: white;
}
.App{
  padding: 10px;
}
.Banner{
  box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 10px;
  padding: 10px;
  margin-bottom: 20px;
  
}
.Application{
  background-color: white;
  padding: 10px;
  margin: 5px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.Calculator{
  padding: 10px;
  background-color: white;
  width: 45%;
  box-shadow: rgb(238, 238, 238) 0px 5px 10px;
}

.results{
  background-color: rgb(235, 235, 235);
  padding: 10px;
  width: 35%;
}

.Years{
  position: relative;
  padding: 10px;
}

.interestrate{
  position: relative;
  padding: 10px;
}

.loanamount{
  padding: 10px;
}

.annual{
  padding: 10px;
}

.calculate{
  padding: 10px;
}

.button {
  background-color: rgb(44, 106, 164);
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button:hover{
  background-color: white;
  color: rgb(44, 106, 164);
}
